@import '../../scss/_mixins';
@import '../../scss/_variables';

.slider {
  @include gridable(100%);
  align-items: center;
  justify-items: center;
  grid-template-areas: 'sliderElement';
  grid-area: slider;
  &__button {
    background-color: transparent;
    width: 48px;
    height: 48px;
    object-fit: cover;
    color: $color-light;
    z-index: 5;
    &_light {
      color: $color-dark;
      border: 2px solid $color-dark;
    }
    &_left {
      grid-area: sliderElement;
      justify-self: start;
      margin-left: 10px;
      background-image: url(../../images/arrow-left.png);
    }
    &_right {
      grid-area: sliderElement;
      justify-self: end;
      margin-right: 10px;
      background-image: url(../../images/arrow-right.png);
    }
  }
}
