@import '../../scss/_mixins';
@import '../../scss/_variables';

.game {
  @include gridable(100%);
  height: 100vh;
  grid-template-areas: 'layer';
  align-items: center;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  grid-area: sliderElement;
  transition: opacity 1s;
  &_active {
    visibility: visible;
    opacity: 1;
  }
  &__info {
    @include gridable(100%);
    height: min-content;
    grid-area: layer;
    max-width: 1024px;
    box-sizing: border-box;
    padding: 30px 60px;
    align-content: center;
    gap: 20px;
    grid-template-areas:
      'heading'
      'text'
      'platform';
    z-index: 5;
    @media screen and (max-width: 1024px) {
      grid-template-areas:
        'heading'
        'text'
        'platform';
      gap: 20px;
      grid-template-columns: 1fr;
    }
  }
  &__image {
    @include flexible(100%);
    object-fit: cover;
    grid-area: layer;
    height: 100vh;
    mask-image: -webkit-gradient(
      linear,
      left bottom,
      left top,
      color-stop(1, transparent),
      color-stop(0.85, rgba($color-dark, 0.6)),
      color-stop(0.65, $color-dark),
      color-stop(0.5, $color-dark),
      color-stop(0.35, $color-dark),
      color-stop(0, $color-dark)
    );
    @media screen and (max-width: 1024px) {
      min-width: 300px;
      max-width: 100%;
    }
  }
  &__heading {
    grid-area: heading;
    font: normal 32px/1 'RussoOne', Arial, sans-serif;
    text-align: center;
    margin: 0;
    max-width: 450px;
    @media screen and (max-width: 375px) {
      font-size: 20px;
    }
  }
  &__text {
    @include flexible(100%);
    flex-flow: column nowrap;
    gap: 10px;
    grid-area: text;
    font-size: 14px;
    box-sizing: border-box;
    max-width: 450px;
    @media screen and (max-width: 450px) {
      font-size: 12px;
    }
  }
  &__paragraph {
    margin: 0;
  }
  &__platform {
    @include gridable(100%);
    grid-template-columns: max-content 1fr;
    align-items: center;
    grid-area: platform;
    gap: 20px;
    font-size: 12px;
    box-sizing: border-box;
    &-heading {
      font: normal 12px/2 'RussoOne', Arial, sans-serif;
      margin: 0;
    }
    &-list {
      @include flexible(100%);
      @include unmarkedList(0, 0);
      gap: 10px;
    }
  }
}
