@import '../../scss/_mixins';
@import '../../scss/_variables';

.promo {
  @include gridable(100%);
  height: 100vh;
  grid-template-areas: 'layer';
  overflow: hidden;
  grid-area: promo;

  &__picture {
    @include flexible(100%);
    grid-area: layer;
    height: 100vh;
    mask-image: -webkit-gradient(
      linear,
      left top,
      left bottom,
      color-stop(0, $color-dark),
      color-stop(0.35, $color-dark),
      color-stop(0.5, $color-dark),
      color-stop(0.65, $color-dark),
      color-stop(0.85, rgba($color-dark, 0.6)),
      color-stop(1, transparent)
    );
  }
  &__image {
    object-fit: cover;
    object-position: center top;
    width: 100%;
  }
  &__info {
    grid-area: layer;
    @include gridable(100%);
    max-width: $max-width;
    box-sizing: border-box;
    padding: $header-height 20px 30px;
    align-content: center;
    gap: 10px;
    height: 100vh;
    z-index: 5;
  }
  &__heading {
    font: normal 48px/1 'RussoOne', Arial, sans-serif;
    margin: 0;
    color: $color-light;
    @media screen and (max-width: 480px) {
      font-size: 36px;
    }
    @media screen and (max-width: 375px) {
      font-size: 24px;
    }
  }
  &__text {
    @include flexible(100%);
    flex-flow: column nowrap;
    grid-gap: 10px;
    font-size: 14px;
    box-sizing: border-box;
    max-width: 50%;
    padding: 10px;
    border-radius: 5px;
    background-color: rgba($color-dark, 0.3);
    @media screen and (max-width: 768px) {
      max-width: 70%;
    }
    @media screen and (max-width: 576px) {
      max-width: 90%;
    }
    @media screen and (max-width: 480px) {
      max-width: 100%;
    }
  }
  &__paragraph {
    margin: 0;
  }
  &__link {
    @include defaultLink();
    grid-area: layer;
    width: 36px;
    height: 36px;
    align-self: flex-end;
    animation-name: drop;
    animation-duration: 2s;
    animation-timing-function: ease;
    animation-iteration-count: infinite;
    background-image: url('../../images/arrow-down.png');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 5;
  }
}

@keyframes drop {
  from {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-20px);
  }
  to {
    transform: translateY(0px);
  }
}
